    <!-- API -->
    <div id="attributes">
      <h3 class="demo-table-title">Attributes</h3>
      <P class="demo-table-introduction"></P>
      <td-table-simple :data="api" :thead="thead" />
    </div>
  </div>
</template>

<script>
export default {
  props: {},
  data() {
    return {
      thead: ['', '', '', '', '', ''],
      anchor: {
        code: {
          html: `
            <td-anchor customize>
              <td-anchor-link href="api" title="API">
                <td-button icon="td-icon-arrow-down" type="primary">API</td-button>
              </td-anchor-link>
            </td-anchor>

