import Button from "../components/Button";
import IconDemo from "../components/Icon.vue";
import Anchor from "../components/Anchor.vue";
import Link from "../components/Link.vue";
import Breadcrumb from "../components/Breadcrumb.vue";
export default [
  {
    path: "/component/icon",
    name: "iconDemo",
    component: IconDemo,
    meta: {
      title: "Icon ",
    },
  },
  // ---------- Basic  -----------
  {
    path: "/component/button",
    name: "button",
    component: Button,
    meta: {
      title: "Button ",
      group: "Basic",
    },
  },
  {
    path: "/component/anchor",
    name: "anchor",
    component: Anchor,
    meta: {
      title: "Anchor ",
    },
  },
  // ---------- Drg-front  -----------
  {
    path: "/component/link",
    name: "link",
    component: Link,
    meta: {
      group: "Drg-front",
      title: "Link ",
    },
  },
  {
    path: "/component/breadcrumb",
    name: "breadcrumb",
    component: Breadcrumb,
    meta: {
      title: "Breadcrumb ",
    },
  },
];
