<template>
  <div class="content-box">
    <!--  -->
    <td-anchor>
      <td-anchor-link href="link-Simple" title="" />
      <td-anchor-link href="link-disabled" title="" />
      <td-anchor-link href="link-jump" title="" />
      <td-anchor-link href="link-underline" title="" />
      <td-anchor-link href="link-icon" title="" />
      <td-anchor-link href="attributes" title="" />
    </td-anchor>

    <h2 class="demo-title">Link </h2>
    <p class="demo-introduction">drg-front,drg-front</p>

    <!--  -->
    <td-demo-block :height="160" :title="''" id="link-Simple" introduction="">
      <template v-slot:demo>
        <div class="codepadding">
          <td-link></td-link>
          <td-link type="primary"></td-link>
          <td-link type="success"></td-link>
          <td-link type="danger"></td-link>
          <td-link type="warn"></td-link>
          <td-link type="info"></td-link>
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(link.code.simple)}}</code>
      </template>
    </td-demo-block>

    <td-demo-block :height="160" :title="''" id="link-disabled" introduction="">
      <template v-slot:demo>
        <div class="codepadding">
          <td-link disabled></td-link>
          <td-link type="primary" disabled></td-link>
          <td-link type="success" disabled></td-link>
          <td-link type="danger" disabled></td-link>
          <td-link type="warn" disabled></td-link>
          <td-link type="info" disabled></td-link>
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(link.code.disabled)}}</code>
      </template>
    </td-demo-block>

    <!--  -->
    <td-demo-block :height="90" :title="''" id="link-jump" introduction="">
      <template v-slot:demo>
        <div class="codepadding">
          <td-link name="installation" :query="{ type: 'index' }" :params="{ index: 1 }"></td-link>
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(link.code.jump)}}</code>
      </template>
    </td-demo-block>

    <!--  -->
    <td-demo-block :height="90" :title="''" id="link-underline" introduction=",">
      <template v-slot:demo>
        <div class="codepadding">
          <td-link underline></td-link>
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(link.code.underline)}}</code>
      </template>
    </td-demo-block>

    <td-demo-block :height="90" :title="''" id="link-icon" introduction="">
      <template v-slot:demo>
        <div class="codepadding">
          <td-link icon="td-icon-arrow-right" suffix-icon="td-icon-arrow-left"></td-link>
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(link.code.icon)}}</code>
      </template>
    </td-demo-block>

    <!-- API -->
    <div id="attributes">
      <h3 class="demo-table-title">Attributes</h3>
      <P class="demo-table-introduction"></P>
      <td-table-simple :data="api" :thead="thead" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      thead: ['', '', '', '', '', ''],
      link: {
        code: {
          simple: `
            <td-link></td-link>
            <td-link type="primary"></td-link>
            <td-link type="success"></td-link>
            <td-link type="danger"></td-link>
            <td-link type="warn"></td-link>
            <td-link type="info"></td-link>
          `,
          disabled: `
            <td-link disabled></td-link>
            <td-link type="primary" disabled></td-link>
            <td-link type="success" disabled></td-link>
            <td-link type="danger" disabled></td-link>
            <td-link type="warn" disabled></td-link>
            <td-link type="info" disabled></td-link>
          `,
          jump: `
            <td-link name="installation" :query="{ type: 'index' }" :params="{ index: 1 }"></td-link>
          `,
          underline: `
            <td-link underline></td-link>
          `,
          icon: `
            <td-link icon="td-icon-arrow-right" suffix-icon="td-icon-arrow-left"></td-link>
          `
        }
      },
      api: [
        {
          parameter: 'type',
          description: '',
          dataTypes: 'String',
          optional: 'default / primary / success  / danger / warn / info',
          default: 'default',
          support: '*'
        },
        {
          parameter: 'name',
          description: 'name,  this.$router.push({name})',
          dataTypes: 'String',
          default: '',
          support: '*'
        },
        {
          parameter: 'alwaysShow',
          description: 'drg-front,',
          dataTypes: 'String',
          default: '',
          support: '*'
        },
        {
          parameter: 'disabled',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true/false',
          default: 'false',
          support: '*'
        },
        {
          parameter: 'underline',
          description: 'hover,',
          dataTypes: 'Boolean',
          optional: 'true/false',
          default: 'false',
          support: '*'
        },
        {
          parameter: 'icon',
          description: ',',
          dataTypes: 'String',
          optional: '',
          default: '',
          support: '*'
        },
        {
          parameter: 'suffix-icon',
          description: ',',
          dataTypes: 'String',
          optional: '',
          default: '',
          support: '*'
        }
      ]
    }
  },
  created() {},
  mounted() {},
  methods: {}
}
</script>

<style scoped lang="scss">
</style>
