<template>
  <div class="content-box">
    <!--  -->
    <td-anchor>
      <td-anchor-link href="breadcrumb-Simple" title="" />
      <td-anchor-link href="breadcrumb-Icon" title="" />
      <td-anchor-link href="breadcrumb-Jump" title="" />
      <td-anchor-link href="breadcrumb-attributes" title="breadcrumb " />
      <td-anchor-link href="breadcrumb-item-attributes" title="breadcrumb-item " />
    </td-anchor>

    <h2 class="demo-title">Breadcrumb </h2>
    <p class="demo-introduction">drg-front,drg-front</p>
    <p class="demo-introduction"></p>

    <!--  -->
    <td-demo-block :height="140" :title="''" id="breadcrumb-Simple" introduction="">
      <template v-slot:demo>
        <div class="codepadding">
          <td-breadcrumb separator="/">
            <td-breadcrumb-item></td-breadcrumb-item>
            <td-breadcrumb-item></td-breadcrumb-item>
            <td-breadcrumb-item></td-breadcrumb-item>
          </td-breadcrumb>
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(breadcrumb.code.simple)}}</code>
      </template>
    </td-demo-block>

    <td-demo-block :height="140" :title="''" id="breadcrumb-Icon" introduction="">
      <template v-slot:demo>
        <div class="codepadding">
          <td-breadcrumb separator-class="td-icon-arrow-right">
            <td-breadcrumb-item></td-breadcrumb-item>
            <td-breadcrumb-item></td-breadcrumb-item>
            <td-breadcrumb-item></td-breadcrumb-item>
          </td-breadcrumb>
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(breadcrumb.code.icon)}}</code>
      </template>
    </td-demo-block>

    <td-demo-block :height="140" :title="''" id="breadcrumb-Jump" introduction=",">
      <template v-slot:demo>
        <div class="codepadding">
          <td-breadcrumb separator="\">
            <td-breadcrumb-item to="installation"></td-breadcrumb-item>
            <td-breadcrumb-item :to="{ path: '/views/standard', query: { type: 'index' }, params: { index: 1 } }"></td-breadcrumb-item>
            <td-breadcrumb-item></td-breadcrumb-item>
          </td-breadcrumb>
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(breadcrumb.code.jump)}}</code>
      </template>
    </td-demo-block>

    <!-- Breadcrumb API -->
    <div id="breadcrumb-attributes">
      <h3 class="demo-table-title">Breadcrumb Attributes</h3>
      <P class="demo-table-introduction"></P>
      <td-table-simple :data="breadcrumbApi" :thead="thead" />
    </div>
     <!-- Breadcrumb Item API -->
    <div id="breadcrumb-item-attributes">
      <h3 class="demo-table-title">Breadcrumb Item Attributes</h3>
      <P class="demo-table-introduction"></P>
      <td-table-simple :data="breadcrumbItemApi" :thead="thead" />
    </div>
  </div>
</template>

<script>
export default {
  props: {},
  data() {
    return {
      thead: ['', '', '', '', ''],
      breadcrumb: {
        code: {
          simple: `
          <td-breadcrumb separator="/">
            <td-breadcrumb-item></td-breadcrumb-item>
            <td-breadcrumb-item></td-breadcrumb-item>
            <td-breadcrumb-item></td-breadcrumb-item>
          </td-breadcrumb>
          `,
          icon: `
          <td-breadcrumb separator-class="td-icon-arrow-right">
            <td-breadcrumb-item></td-breadcrumb-item>
            <td-breadcrumb-item></td-breadcrumb-item>
            <td-breadcrumb-item></td-breadcrumb-item>
          </td-breadcrumb>
          `,
          jump: `
          <td-breadcrumb separator="\">
            <td-breadcrumb-item to="installation"></td-breadcrumb-item>
            <td-breadcrumb-item :to="{ path: '/views/standard', query: { type: 'index' }, params: { index: 1 } }"></td-breadcrumb-item>
            <td-breadcrumb-item></td-breadcrumb-item>
          </td-breadcrumb>
          `
        }
      },
      breadcrumbApi: [
        {
          parameter: 'separator',
          description: '',
          dataTypes: 'String',
          default: '/',
          support: 'breadcrumb'
        },
        {
          parameter: 'separator-class',
          description: ' class',
          dataTypes: 'String',
          default: '',
          support: 'breadcrumb'
        }
      ],
      breadcrumbItemApi: [
        {
          parameter: 'to',
          description: ' vue-router  to',
          dataTypes: 'Object/String',
          default: '',
          support: 'breadcrumb-item'
        },
        {
          parameter: 'replace',
          description: ' to  replace  history ',
          dataTypes: 'boolean',
          default: 'false',
          support: 'breadcrumb-item'
        },
        {
          parameter: 'forward',
          description: ' vue-router  this.$router.go(-forward)',
          dataTypes: 'Number',
          default: '',
          support: 'breadcrumb-item'
        }
      ]
    }
  },
  created() {},
  mounted() {},
  methods: {}
}
</script>

<style scoped lang="scss">
</style>
